
import food from '@/components/food.vue'
import share from '@/components/share.vue'

import {jsdkConfig} from './../api/jsdkConfig'
import {appId} from './../api/config'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default{
data(){
return {
showFood: false,
showShare: false,
tabIndex: 1,
showForm: false,
shareImg: require('./../assets/hao.png')
}
},
methods: {
getWxCoifig(){
jsdkConfig({url: encodeURIComponent(window.location.href.split('#')[0])}).then((res) => {
if (res.success) {

wx.config({
debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: appId, // 必填，公众号的唯一标识
timestamp: res.timestamp, // 必填，生成签名的时间戳
nonceStr: res.noncestr, // 必填，生成签名的随机串
signature: res.signature, // 必填，签名，见附录1
jsApiList: [
'onMenuShareTimeline','wx.onMenuShareQZone',
'onMenuShareAppMessage','onMenuShareQQ'
]
});
this.share()
} else {
console.warn('getWxCoifig,失败');

}
});
},
share(shareObj = {}){
let _tthis
let obj = {
title: '风景网旅游卡',
desc: '旅游年卡，说走就走',
link: window.location.href.split('#')[0]+'#'+window.location.href.split('#')[1]+"?id=99",
imgUrl: this.shareImg,
sucsess(){
_this.showShare=false
}
}

wx.ready(function () {
wx.onMenuShareTimeline(obj);        
wx.onMenuShareAppMessage(obj);      
wx.onMenuShareQQ(obj);              
wx.onMenuShareQZone(obj);           
});
wx.error(function (res) {

});

},
closeCover(){
this.showFood = false
},
closeShareCover(){
this.showShare = false
},
addFood(val){
console.log(val)
this.showForm = true
}
},
created(){
document.title = '年卡订购'
this.getWxCoifig()
},
components: {
food, share, Tabbar,
TabbarItem,

}
}



